import styled from "styled-components";

const SRunLine = styled.div`
  padding: 20px 0;
  width: 100%;
  margin-bottom: 180px;
  display: flex;
  align-items: center;
  gap: 50px;
  overflow: hidden;
  user-select: none;
  pointer-events: none;
`;

const TagContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 23px;

  animation: run-text 50s infinite linear;

  @keyframes run-text {
    0% {
      transform: translate(0, 0);
    }

    100% {
      transform: translate(calc(-100% - 50px), 0);
    }
  }
`;

const TagBox = styled.p`
  white-space: nowrap;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #0760A8;
`;

const RunLine = () => {
  const Tags = [
    "#нативная реклама",
    "#mobile-fullscreen",
    "#телеграм #CPM",
    "#in-stream",
    "#CPV",
    "#вконтакте",
    "#видео-реклама",
    "#однокласники",
    "#YouTube",
    "#дзен",
    "#CPA",
    "#аналитика",
    "#brand safety",
  ];

  return (
    <SRunLine>
      <TagContainer>
        {Tags.map((tag) => (
          <TagBox key={Math.random()}>{tag}</TagBox>
        ))}
      </TagContainer>
      <TagContainer>
        {Tags.map((tag) => (
          <TagBox key={Math.random()}>{tag}</TagBox>
        ))}
      </TagContainer>
      <TagContainer>
        {Tags.map((tag) => (
          <TagBox key={Math.random()}>{tag}</TagBox>
        ))}
      </TagContainer>
    </SRunLine>
  );
};

export default RunLine;
